import React, {Component} from 'react';
import '../sass/style.css';
import mypicture from '../img/about/foto_sma.jpg';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Col, Container, Row} from "reactstrap";
import PropTypes from "prop-types";

class About extends Component {
    render() {
        return (
            <section className="about" id="about">
                <Container className="themed-container">
                    <Row>
                        <Col sm="12">
                            <h2 className="text-center"><b>About</b></h2>
                        </Col>
                    </Row>
                    <Row id="about-content">
                        <Col sm="6">
                            <p>
                                Hai namaku Adam, kalian bisa memanggilku Arthur. Saat ini aku sedang berkuliah di
                                Universitas
                                Sebelas Maret Surakarta, program studi Teknik Informatika angkatan 2019. Umurku baru 17
                                tahun dan alamat
                                rumahku di Karanganyar.
                            </p>
                        </Col>
                        <Col sm={{size: 5, offset: 1}}>
                            <div className="my_Pic img-fluid">
                                <img className="img-thumbnail" id="my_Pic" src={mypicture} alt="Foto SMA"/>
                            </div>
                        </Col>
                    </Row>
                </Container>
            </section>
        );
    }
}

Container.propTypes = {
    fluid: PropTypes.oneOfType([PropTypes.bool, PropTypes.string])
    // applies .container-fluid class if bool or .container-{breakpoint} if string
}

Row.propTypes = {
    noGutters: PropTypes.bool,
    // see https://reactstrap.github.io/components/form Form Grid with Form Row
    form: PropTypes.bool,
    xs: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    sm: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    md: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    lg: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
    xl: PropTypes.oneOfType([PropTypes.number, PropTypes.string])
}

const stringOrNumberProp = PropTypes.oneOfType([PropTypes.number, PropTypes.string]);
const columnProps = PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.bool,
    PropTypes.shape({
        size: PropTypes.oneOfType([PropTypes.bool, PropTypes.number, PropTypes.string]),
        // example size values:
        // 12 || "12" => col-12 or col-`width`-12
        // auto => col-auto or col-`width`-auto
        // true => col or col-`width`
        order: stringOrNumberProp,
        offset: stringOrNumberProp
    })
]);

Col.propTypes = {
    xs: columnProps,
    sm: columnProps,
    md: columnProps,
    lg: columnProps,
    xl: columnProps,
    // override the predefined width (the ones above) with your own custom widths.
    // see https://github.com/reactstrap/reactstrap/issues/297#issuecomment-273556116
    widths: PropTypes.array,
}

export default About;